<template>
  <v-detail :songs="isongs" :singer="disc"></v-detail>
</template>
<script type="text/javascript">
  import {mapGetters} from 'vuex'
  import {getSongList} from 'api/recommend'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  import VDetail from 'base/detail/detail'

  export default{
    data() {
      return {
        isongs: []
      }
    },
    mounted() {
    },
    computed: {
      ...mapGetters([
        'disc'
      ])
    },
    created() {
      this._getSongList()
    },
    methods: {
      _getSongList() {
        if (!this.disc.dissid) {
          this.$router.push('/recommend')
          return
        }
        getSongList(this.disc.dissid)
        .then((res) => {
          if (res.code === ERR_OK) {
            this.isongs = this._normaSongs(res.cdlist[0].songlist)
            // this.isongs = this._normaSongs(this.isongs)
          }
        })
      },
      _normaSongs(list) {
        let ret = []
        list.forEach((musicData) => {
          if (musicData.songid && musicData.albummid) {
            ret.push(createSong(musicData))
          }
        })
        return ret
      },
      _balck() {
        this.$router.back()
      },
      _randomPlay() {
        this.randomPlay({
          list: this.isongs
        })
      }
    },
    components: {
      VDetail
    }
  }
</script>


