<template>
  <transition name="detail">
    <div class="detailBody" ref="detailBody">
      <header class="ellipsis detailheader"><span @click="_balck" class="iconfont icon-left detailBack"></span><h1>{{ singer.dissname || singer.name || singer.topTitle }}</h1></header>
      <div class="detailCoverPic">
      <img :src="detailBag" ref="detailImg">
      <div class="color-cover"></div>
      </div>
      <div class="overHiden">
        <div class="play" v-show="songs.length>=2"><p class="play-border" @click="_randomPlay"><span class="iconfont icon-bofang play-icon"></span><span class="play-text">随机播放全部</span></p></div>
        <div class="detailScroll">
          <v-scroll v-on:updateY="enlage">
            <ul class="detailUl" id="detailUlId">
              <li v-for="(item, index) in songs" @click="_secectItem(index)" :class="{rankLi: rank}" class="detailLi">
                <i class="iconfont" :class="{'icon-1':index === 0, 'icon-2':index === 1, 'icon-3':index === 2}"
                v-if="rank && index < 3"></i>
                <span v-if="rank && index >= 3" class="rank-span">{{ index + 1 }}</span>
                <p class="ellipsis">{{ item.name }}</p>
                <p class="ellipsis"><span>{{ item.album }}&nbsp;·&nbsp;{{ item.singer }}</span></p>
              </li>
              <li id="footer" v-show="isShowFooter"><span class="author">我是有底线的</span> <span class="email">email：530366891@qq.com</span></li>
            </ul>
          </v-scroll>
        </div>
      </div>
    </div>
  </transition>
</template>
<script type="text/javascript">
  import {mapActions} from 'vuex'
  import VScroll from 'base/scroll/scroll'

  export default{
    props: {
      songs: {
        type: [Array, Boolean],
        default: false
      },
      singer: {
        type: [Array, Object],
        default: function() {
          return []
        }
      },
      rank: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        isShowFooter: false,
        detailBag: ''
      }
    },
    mounted() {
    },
    watch: {
      songs() {
        if (this.rank) {
          this.detailBag = this.songs[0].image
        } else {
          this.detailBag = this.singer.avatar || this.singer.imgurl
        }
        this.isShowFooter = true
      }
    },
    methods: {
      _secectItem(index) {
        console.log(this.songs)
        this.selectPlay({
          list: this.songs,
          index: index
        })
      },
      enlage(res) {
        if (res < 0) {
          return
        }
        this.$refs.detailImg.style.transform = `scale(${Math.min(1 + res / 300, 1.5)})`
      },
      _showFooter() {
        this.isShowFooter = true
      },
      _balck() {
        this.$router.back()
      },
      _randomPlay() {
        this.randomPlay({
          list: this.songs
        })
      },
      ...mapActions([
        'selectPlay',
        'randomPlay'
      ])
    },
    components: {
      VScroll
    }
  }
</script>
<style scoped>
	.detailBody{
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 7;
    background-size: 100% auto;
    background: #333;
    color: #fff;
  }
  .detailCoverPic {
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    top: 0;
    height: 100%;
    z-index: 6;
    background: #333;
  }
  .detailCoverPic img {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    z-index:6;
    width: 100%;
    height: auto;
    filter: blur(0px);
    transform-origin: top;
    transform: scale(1);
    background: #333;
  }
  .color-cover {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index:6;
    width: 100%;
    background: #07111b;
    opacity: .5;
  }
  .detail-enter-active, .detail-leave-active{
    transition: all .3s;
  }
  .detail-enter, .detail-leave-to{
    transform: translate3d(100%,0,0);
  }
  .overHiden{
    width: 100%;
    position: absolute;
    top: 40px;
    bottom: 0px;
    height: 100%;
    overflow: hidden;
  }
  .detailheader{
    height: 40px;
    line-height: 40px;
    width: 100%;
    text-align: center;
    position: fixed;
    z-index: 7;
    top: 0;
    overflow: hidden;
  }
  .detailheader h1{
    font-size: 18px;
    color: #fff;
    text-indent: -40px;
  }
  .detailBack{
    float: left;
    width: 40px;
    font-size: 20px;
    font-weight: 800px;
    color: #ffcd32;
  }
  .detailScroll{
    position: absolute;
    bottom: 0;
    height: 70%;
    width: 100%;
    z-index: 7;
  }
  .detailUl{
    width: 100%;
    /*overflow: hidden;*/
    padding-bottom: 40px;
  }
  .detailLi{
    width: 100%;
    height: 50px;
    padding: 20px 0 0 30px;
    background: #222;
    background-color: #222;
    margin: -1px 0;
  }
  .detailLi p {
    width: 85%;
    height: 25px;
    line-height: 25px;
    font-size: 15px;
    font-weight: 400;
    color: #fff;
  }
  #detailUlId .rankLi {
    background: #222;
    background-color: #222;
    margin: 0;
 }
 #detailUlId .rankLi i {
  float: left;
  height: 50px;
  line-height: 50px;
  width: 10%;
  margin-right: 5%;
  text-align: center;
  font-size: 25px;
  /*color: #ffcd32;*/
  min-width: 30px;
}
#detailUlId .rankLi p {
  float: left;
  width: 75%;
  height: 25px;
  line-height: 25px;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
}
#detailUlId .rankLi .rank-span {
  float: left;
  height: 50px;
  line-height: 50px;
  width: 10%;
  margin-right: 5%;
  text-align: center;
  font-size: 18px;
  color: #ffcd32;
  min-width: 30px;
}
#detailUlId .rankLi .icon-1{
  color: #FFD700
}
#detailUlId .rankLi .icon-2{
  color: #DCDCDC
}
#detailUlId .rankLi .icon-3{
  color: #E3A869
}
.detailUl li span{
  line-height: 25px;
  font-size: 13px;
  color: hsla(0,0%,100%,.3);
  font-weight: 400;
}
.play{
  width: 100%;
  height: 30px;
  line-height: 30px;
  position: absolute;
  top: 22%;
  text-align: center;
  z-index: 6;
}
.play-border{
  display: inline-block;
  width: 130px;
  height: 100%;
  border-radius: 20px;
  border: solid .1rem #ffcd32;
  color: #ffcd32;
  line-height: 30px;
}
.play-border .play-icon{
  font-size: 15px;
}
.play-border .play-text{
  display: inline-block;
  font-size: 12px;
  text-indent: 5px;
}
</style>


